const Login = (props) => {
  const [cookies, setCookie] = useCookies({}); //  
  const navigate = useNavigate();

  const [form, setForm] = useState({
    id: "",
    password: "",
  });

  const handleChange = useCallback(
    (e) => {
      const { name } = e.target;
      setForm({
        ...form,
        [name]: e.target.value,
      });
    },
    [form]
  );

  console.log(form.id);
  // const login = (e) => {
  //   e.preventDefault();
  //   axios
  //     .post("/users/login", {
  //       //  
  //       id: formRef.current.id.value,
  //       password: formRef.current.passWord.value,
  //     })
  //     .then((res) => {
  //       setCookie("id", res.data.token); //   
  //     });
  // };

  const login = (e) => {
    e.preventDefault();
    if (form.id === serverDummy.id && form.password === serverDummy.password) {
      // 
      for (let key in tokenDummy) {
        setCookie(key, tokenDummy[key]);
      }
      navigate("/home"); //     
    } else {
      alert("   .");
    }
  };

  const handleClick = () => {
    navigate("/signup");
  };

  return (
    <LoginWrapper>
      <Container>
        <Logo>
          <LogoImage src={logoImage} alt="" />
        </Logo>
        <Box>
          <BoxContainer>
            <Title>test</Title>
            <Form>
              <Input
                type="email"
                name="id"
                placeholder=""
                required
                onChange={handleChange}
              />
              <Input
                type="password"
                name="password"
                placeholder=""
                required
                onChange={handleChange}
              />
              <MainButton
                color={"main"}
                fullWidth={true}
                marginTop={1}
                onClick={login}
                disabled={form.id === "" || form.password === "" ? true : false}
                // marginTop={"1rem"}
              >
                
              </MainButton>
            </Form>
          </BoxContainer>
        </Box>
        <SignUpButton onClick={handleClick}></SignUpButton>
      </Container>
    </LoginWrapper>
  );
};

