import React from "react";
import styled, { css } from "styled-components";

//outline, fullWidth, color, size   .
//margin         margin.     ,       .
// ex) <MainButton outline fullWidth color="blue" size="large">Button</MainButton>
// ex> <MainButton color="blue" size="large">Button</MainButton>

const colorStyles = css`
  ${({ color }) => {
    return css`
      background-color: var(${`--${color}`});
      &:hover {
        background-color: var(${`--${color}`});
        opacity: 0.8; // lighten   
      }
      &:active {
        background-color: var(${`--${color}`});
        opacity: 1;
      }
      ${(props) =>
        props.outline &&
        css`
          color: var(${`--${color}`});
          background: none;
          &:hover {
            background-color: var(${`--${color}`});
            color: white;
          }
        `}
    `;
  }}
`;

const sizes = {
  large: {
    height: "3rem",
    fontSize: "large",
  },
  medium: {
    height: "2.5rem",
    fontSize: "medium", //medium == 1rem
  },
  small: {
    height: "2rem",
    fontSize: "small",
  },
};

const sizeStyles = css`
  ${({ size }) => css`
    height: ${sizes[size].height};
    font-size: ${sizes[size].fontSize};
  `}
`;

//fullWidth true   ,  80%   
const fullWidthStyle = css`
  ${(props) =>
    props.fullWidth &&
    css`
      width: 100%;
      justify-content: center;
      & + & {
        margin-left: 0;
      }
    `}
`;

const StyledButton = styled.button`
  /*   */
  /* display: inline-flex; */

  outline: none;
  border-style: none;
  border-radius: 4px;
  color: white;
  padding-left: 1rem;
  padding-right: 1rem;

  /*  */
  ${sizeStyles}

  /*  */

  margin-left: ${({ marginLeft }) => `${marginLeft}rem`};
  margin-right: ${({ marginRight }) => `${marginRight}rem`};
  margin-top: ${({ marginTop }) => `${marginTop}rem`};
  margin-bottom: ${({ marginBottom }) => `${marginBottom}rem`};

  ${fullWidthStyle}

  &.active {
    /*  */
    ${colorStyles}
    cursor: pointer;
  }

  &.deactive {
    background-color: #f0f0f0;
    color: #909090;
    cursor: default;
  }
`;

function MainButton({
  children,
  color,
  size,
  outline,
  fullWidth,
  marginBottom,
  marginLeft,
  marginRight,
  marginTop,
  disabled = false,
  ...rest
}) {
  return (
    <StyledButton
      color={color}
      size={size}
      outline={outline}
      fullWidth={fullWidth}
      marginTop={marginTop}
      marginBottom={marginBottom}
      marginLeft={marginLeft}
      marginRight={marginRight}
      disabled={disabled}
      className={disabled ? "deactive" : "active"}
      {...rest}
    >
      {children}
    </StyledButton>
  );
}

MainButton.defaultProps = {
  color: "#3CC23F",
  size: "medium",
};

export default MainButton;
