//check and logout
import React, { useEffect, useState } from "react";
import { useCookies } from "react-cookie";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";

const ExpiredCheck = (props) => {
  const [isLogined, setIsLogined] = useState(null);
  const [cookies, removeCookie] = useCookies(null);
  const navigate = useNavigate();

  const isExpired = (expiredAt) => {
    let now = new Date();
    // console.log(" " + now);
    let expiredAtDate = new Date(expiredAt);
    // console.log(" " + expiredAtDate);
    if (now > expiredAtDate) {
      return true;
    } else {
      return false;
    }
  };

  //  
  const authCheck = () => {
    const exp = cookies.expiredAt;
    //     
    if (!exp || isExpired(exp)) {
      for (let key in cookies) {
        removeCookie(key); //  
      }
      navigate("/"); //   
    }
    //    
    else {
      setIsLogined(cookies.name);
    }
  };

  useEffect(() => {
    authCheck(); //   
  });

  const logOut = () => {
    for (let key in cookies) {
      removeCookie(key); //  
    }
    navigate("/"); //   
  };

  return (
    <>
      {/* isLogined props      */}
      {isLogined && <Div>{isLogined} !</Div>}
      <button onClick={logOut}></button>
    </>
  );
};

const Div = styled.div`
  font-weight: 700;
  font-size: 1.3rem;
`;
export default ExpiredCheck;
